export default{
	props:{
		expressData:Object,
		
	},
	
	data() {
		return {
			e_time:''
		}
	},
	computed:{
	},
	filters: {
	    // formatDate(time) {
	    //   let date = new Date(time)
	    //   return this.$moment(time).format(YYYY-MM-DD);
	    // }
	  },
	  methods:{
		  getTime:function(){
		  var date = new Date(),
		  // year = date.getFullYear(),
		  // month = date.getMonth() + 1,
		  day = date.getDate(),
		  hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours(),
		  minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes(),
		  second = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
		  // month >= 1 && month <= 9 ? (month = "0" + month) : "";
		  // day >= 0 && day <= 9 ? (day = "0" + day) : "";
		  var timer = hour + ':' + minute + ':' + second;
		  return timer;
		  },
		  gotoSettlement(){
			  uni.navigateTo({
			  	url:'../orderParticulars/orderParticulars?id='+this.expressData.eId
			  })
		  }
	  },
	 mounted() {
	    let that = this; // 声明一个变量指向Vue实例this，保证作用域一致
	    this.timer = setInterval(() => {
	      that.e_time = this.getTime(); // 修改数据date
	    }, 1000)
	  },
	  beforeDestroy() {
	    if (this.timer) {
	      clearInterval(this.e_time); // 在Vue实例销毁前，清除我们的定时器
	    }
	  }
}
